const submenuLinks = document.querySelectorAll('.main-nav__link--submenu'); // все ссылки с подменю
const submenus = document.querySelectorAll('.submenu'); // все подменю

//clickedLink - ссылка с подменю, по к-ой щелкнули
submenuLinks.forEach((clickedLink) => {
  clickedLink.addEventListener('click', (e) => {
    e.preventDefault();
    const submenuWrapper = clickedLink.parentNode; // родитель ссылки, по к-ой щелкнули
    const submenu = submenuWrapper.querySelector('.submenu'); // внутри родителя находим подменю

    // закрываем все открытые меню по новому щелчку
    for (let link of submenuLinks) {
      if (link.classList.contains('main-nav__link--submenu-closed') && link !== clickedLink) {
        link.classList.remove('main-nav__link--submenu-closed');
        for (let menu of submenus) {
          if (menu.classList.contains('main-nav__submenu--opened')) menu.classList.remove('main-nav__submenu--opened');
        }
      }
    }

    // открываем подменю
    submenu.classList.toggle('main-nav__submenu--opened');
    clickedLink.classList.toggle('main-nav__link--submenu-closed');

  });
});

// закрываем подменю по щелчку в стороне
document.body.addEventListener('click', (e) => {
  let target = e.target;
  if (!target.classList.contains('main-nav__link--submenu')) {
    for (let menu of submenus) {
      menu.classList.remove('main-nav__submenu--opened');
    }
    for (let link of submenuLinks) {
      link.classList.remove('main-nav__link--submenu-closed');
    }
  }
});
